
.header{
    display: flex;
    justify-content: space-between;
    margin: 10px;
    border: 3px solid red;
}
.nav-items>ul{
    list-style-type: none;
    display: flex;
}
.nav-items>ul>li{
    padding: 18px;
    cursor: pointer;
}
.logo{
    width: 70px;
}
.card1{
    width: 200px;
    border: 2px solid black;
    padding: 10px;
    margin: 20px;
    text-align: center;
    /* box-shadow: 2px 2px 10 rgba(12, 12, 12, 0.8); */
}
.card1>img{
    width:100%;
    height: 200px;
    border-radius: 10px;
    /* box-shadow: 2px 2px 10 rgba(12, 12, 12, 1); */
}
.restaurant-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    /* border: 2px solid black; */
}
.shimmer-card{
    width: 200px;
    height: 200px;
    background-color: lightgray;
    margin: 20px;
}

.menu{
    display: flex;
}
